
.page-nav {
  padding: 10px 0;
  color: #34495e;
  font-size: 1em;
}
.page-nav__btn {
  cursor: pointer;
}
.page-nav__btn:first-child {
  margin-left: auto;
}
.page-nav__btn:last-child {
  margin-right: auto;
}
.page-nav__btn--disabled {
  color: #ccc;
  cursor: not-allowed;
}
.page-nav__num {
  margin: 10px;
}


