
.news-item {
  list-style: none;
  padding: 20px 30px 20px 80px;
  border-bottom: 1px solid #eee;
  position: relative;
  line-height: 20px;
}
.news-item .score {
  color: #f60;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -10px;
  width: 80px;
  font-weight: 700;
}
.news-item p {
  text-align: left;
  margin: 0;
}
.news-item p > a {
  color: #34495e;
  text-decoration: none;
}
.news-item p > span {
  font-size: 0.85em;
  color: #828282;
  margin-left: 5px;
}
.news-item .meta {
  text-align: left;
  color: #828282;
  font-size: 0.85em;
}
.news-item .meta span {
  margin-right: 10px;
}
.news-item .meta span > a {
  margin-left: 5px;
  color: #828282;
}
.news-item .meta span > a:hover {
  color: #f60;
}


