
.comment {
  padding: 15px 0;
  border-top: 1px solid #eee;
}
.comment__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #828282;
  margin-bottom: 10px;
  font-size: 0.9rem;
}
.comment__header > a {
  color: #828282;
  text-decoration: underline;
  margin-right: 5px;
}
.comment__text {
  color: #34495e;
  line-height: 1.4em;
  overflow-wrap: break-word;
  font-size: 0.95rem;
  margin-bottom: 10px;
}
.comment__text a {
  color: #34495e;
}
.comment__text a:hover {
  color: #f60;
}
.comment__children {
  padding-left: 20px;
}
.comment__children__info {
  background-color: #fff4dc;
  color: #828282;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 3px;
}
.comment__children__toggle {
  position: relative;
  left: -20px;
  top: 8px;
  color: #828282;
  cursor: pointer;
}


