
.fade-enter-active {
  transition: opacity 2s;
}
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.page-nav {
  background: #fff;
  width: 100%;
  position: fixed;
  top: 90px;
  z-index: 100;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.news-list {
  max-width: 800px;
  margin: 60px auto 50px;
  background: #fff;
  padding: 0;
  border-radius: 5px;
}


