
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}
.container header {
  background: #f60;
}
.container header nav {
  max-width: 800px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: auto;
  color: #fff;
  padding: 20px 0;
}
.container header nav > a {
  text-decoration: none;
  margin-right: 30px;
  font-weight: 500;
  color: #fff;
}
.container header nav .router-link-active {
  font-weight: 700;
}
.container header img {
  width: 24px;
  margin-right: 30px;
}
.container header p {
  margin-left: auto;
}
.container .main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: auto;
}
.container .main .main-container > ul {
  padding: 0;
}


