
.item {
  width: 80%;
  max-width: 800px;
  margin: auto;
  text-align: left;
}
.item > ._loading {
  margin: 100px 0;
}
.item__header {
  background-color: #fff;
  margin-bottom: 20px;
  padding: 30px 20px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.item__header__title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.item__header__title > a {
  font-size: 1.5rem;
  color: #34495e;
  text-decoration: none;
  margin-right: 20px;
  font-weight: bold;
}
.item__header__title > span {
  color: #828282;
}
.item__header__meta {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #828282;
  margin-top: 15px;
}
.item__header__meta > * {
  margin-right: 8px;
}
.item__header__meta > a {
  text-decoration: underline;
  color: #828282;
}
.item__comments {
  position: relative;
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
  padding: 0 20px;
}
.item__comments > .loader {
  position: absolute;
  top: 10px;
  left: 190px;
}
.item__comments__header {
  padding: 15px 0;
  color: #34495e;
  font-size: 1.2rem;
}


