
.fade-enter-active {
  transition: opacity 2s;
}
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.user-container {
  background: #fff;
  max-width: 800px;
  margin: auto;
  padding: 2em 3em;
  color: #34495e;
}
.user-container h1 {
  text-align: left;
  margin: 0;
  font-size: 1.5em;
}
.user-container .meta {
  padding: 0;
  list-style: none;
  text-align: left;
  font-size: 1em;
}
.user-container .about {
  margin: 1em 0;
}
.user-container .links {
  text-align: left;
}
.user-container .links a {
  font-size: 1em;
  color: #34495e;
}


